.menuNav {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.menuUl {
  width: 100%;
  min-width: 1450px;
  background-color: tomato;
  display: flex;
  height: 50px;
}

.menuUl li {
  border-bottom: 1px solid black;
  border-right: 1px solid black;
  width: calc(100% / 4);
  padding: 15px;
  text-align: center;
  cursor: pointer;
  font-weight: 600;
}

.menuUl li:last-child {
  border-right: none;
}

.sideNavBar {
  text-align: center;
  width: 230px;
  margin-top: 50px;
  background-color: tomato;
  min-height: 100vh;
  padding: 100px 20px;
  border-right: 1px solid black;
}

.picture {
  background-color: white;
  margin: 20px auto;
  height: 70px;
  width: 70px;
  border-radius: 70px;
}

.menuName {
  text-align: left;
  margin: 100px 50px;
}

.menuName li {
  cursor: pointer;
  margin: 20px 0px;
  font-size: 1.3rem;
  font-weight: 600;
}
