.disFlex {
  display: flex;
  flex-wrap: wrap;
}

.disBox {
  width: calc(50% - 82px);
  margin: 20px;
  padding: 20px;
  border: 1px solid black;
  text-align: center;
  border-radius: 10px;
}

.welcom {
  text-align: center;
  font-size: 3rem;
  margin: 50px 20px;
  padding: 15px;
  border: 1px solid black;
  border-radius: 10px;
  background-color: rgba(245, 245, 220, 0.781);
}

.summery {
  margin: 15px 0px 20px;
}

.genres {
  display: flex;
  justify-content: center;
}

.genres li {
  margin: 0px 10px;
}

.detailContainer {
  display: flex;
}

.detailContent {
  margin: 100px 50px;
  width: calc(100% - 380px);
}

.detailContent * {
  margin: 10px 0px;
}

.container {
  width: calc(100% - 270px);
}
.movie__content {
  margin-top: 100px;
}
