*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  list-style-type: none;
  color: black;
  text-decoration-line: none;
}

body {
  min-width: 1450px;
  letter-spacing: -1px;
  font-size: 17px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  box-sizing: border-box;
  background-color: rgb(255, 146, 127);
}

#root {
  display: flex;
  position: relative;
}
